import './App.css';
import Child from './Child';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Tabs from '@mui/material/Tabs';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Child2 from './Child2';
import List from './List';
import Form from './Form';
import LoginPage from './LoginForm';
import UseEffect from './hooks/UseEffect';
import Products from './Products/Products';


function App() {
  const colorValue = ( value ) => {
    const mainDiv = document.getElementById( "main-div" )
    mainDiv.style.backgroundColor = value;
  }
  const isPresent = false;
  if ( isPresent ) {
    return (
      <>
        <div className="App">

          <h1 style={{ color: "red" }}>Hello Style!</h1>

          <Stack spacing={2}>
            <Container maxWidth="sm">
              <Box id="main-div" sx={{ bgcolor: '#cfe8fc', height: '50vh', }} />
            </Container>
            <Tabs centered >
              <Stack spacing={2} direction="row" centered>
                <Button style={{ background: "#7e57c2", color: "white" }} variant="outlined">Outlined</Button>
                <Button variant="contained">Container</Button>
              </Stack>
            </Tabs>
          </Stack>
          <Form />


          {isPresent ? <Child changedColor={colorValue} /> : <> <List /> <Child2 />  </>}


        </div>
      </>
    );
  }
  else {

    return (
      <>
        <Products/>
        
      </>
    )



  }
}

export default App;
